var request = require("@/common/service.js");
var _self;
export default {
     data() {
         return {
				iconsel: "/static/selected.png",
				iconNosel: "/static/static.png",
				 isCheckState: 0,
				 payStype:[
                 { name:"微信支付", image:require('@/static/weixin.png'),cont:'快捷安全，支持微信支付'},
                 // { name:"支付宝支付", image:require('@/static/weixin.png'),cont:'快捷安全，支持银行卡支付'}
				],
				order_sn:'',
				fee:''
			}
		},
		onLoad(option){
		  _self = this;
		  _self.order_sn=option.order_sn;
		  _self.result = uni.getStorageSync('RESULT');
		  request.setHToken(_self.result)
		},
		methods: {
			zcbtn: function() {
				_self = this;
				let data={
					fee:_self.fee
				}
				console.log(data)
				request.rechargeSetOrder(data,function(res){
						  if(res.code==1){
							  _self.order_sn=res.data.order_sn;
								uni.login({
									provider: 'weixin',
									success: function(loginRes) {
										let code = loginRes.code;
										// 请求后台数据 prepay_id paySign nonceStr timeStamp
										uni.request({
											url: 'https://fenxiao.99zhifukeji.com/api/recharge/pay',
											data: {
												code: code,
												order_sn:_self.order_sn
											},
											header: {//自定义请求头信息
												'custom-header': 'application/json',
												 'token':_self.result
											},
											success: (res) => {
												console.log(res)
												//调起支付
												uni.requestPayment({
													provider: 'wxpay',
													timeStamp: res.data.data.timeStamp,
													nonceStr: res.data.data.nonceStr,
													package: res.data.data.package,
													signType: 'MD5',
													paySign: res.data.data.sign,
													success: function(res) {
														console.log()
														uni.showToast({
															icon: 'none',
															title: res.msg
														});
														setTimeout(function(){
															uni.reLaunch({
															  url: '../user/user' 
															});
														 },1000)
														console.log('success:' + JSON.stringify(res));
													},
													fail: function(err) {
														console.log('fail:' + JSON.stringify(err));
													}
												});
											}
										});
									}
								});
						  }else{
							uni.showToast({
								icon: 'none',
								title: res.msg
							});
						} 
				},function(err){
					 uni.hideLoading();
					 uni.showToast({
						 icon: 'none',
						 title: err.msg,
					 });
				}) 
			
			},
			zhifuChange(index){
				this.isCheckState = index;
			}
		}
}
